<template>
  <div style="max-width: 100%;box-sizing: border-box;">
    <myHeader
      :isBack="true"
      backPath="/castSeedsRecord"
      title="投苗申请详情">
    </myHeader>
    <div style="padding:.12rem 0;background:#f8f8f8;" class="mt4">
      <mt-cell title="养户：" :value="name"></mt-cell>
      <mt-cell title="饲养规模：" :value="size+'头'"></mt-cell>
      <mt-cell title="申请投苗日期：" :value="castData.applyDate"></mt-cell>
      <mt-cell title="申请投苗数量：" :value="castData.num"></mt-cell>
      <div v-if="castData.actDate">
        <mt-cell title="实际投苗时间：" :value="castData.actDate"></mt-cell>
        <mt-cell title="实际投苗数量：" :value="castData.actNum"></mt-cell>
        <mt-cell title="实际投苗均重：" :value="castData.actAvgWg"></mt-cell>
      </div>
    </div>
    <div v-for="(option,index) in options">
      <mt-cell :title="option.value || option" class="item-checkbox-body">
        <span class="zb-checkbox">
          <span>
            <input
              type="checkbox"
              :value="option.value || option"
              class="checkboxItem"
              :id="'check'+index"
              disabled
            >
            <label :for="'check'+index"></label>
          </span>
        </span>
      </mt-cell>
    </div>
    <Signature v-show="isShowSignature" ref="signature"></Signature>
  
    <SubmitBtn v-if="canSigner == 1 && billStatusIndex==30 && recordState == 1"
               @click.native="submitData()"></SubmitBtn>
  </div>
</template>
<script>
  import myHeader from '@/components/Header'
  import SubmitBtn from '@/components/SubmitBtn'
  import Signature from '@/components/Signature'
  import {queryInfoById, signerTouMiao} from '@/api/business'
  import {mapGetters} from "vuex"
  import {getParam} from '@/utils/wxUtil'
  import {Indicator} from 'mint-ui'

  export default {
    name: 'CastSeedsApply',
    components: {
      myHeader,
      SubmitBtn,
      Signature
    },
    data() {
      return {
        castData: {},
        options: [
          '消毒工作是否准备',
          '保温箱、屋中屋是否准备（底板实 木板侧板压缩版，每头猪面积不少 于0.1平方）',
          '保温灯是否准备（每栏1个，护理 栏2个）',
          '发电机是否正常使用（15KW以上）',
          '圆盘料槽是否准备（50头/个）',
          '物资准备（每100头猪：米糠100斤、生石灰100斤、米25斤、奶粉2斤、生姜10斤、红糖10斤）',
          '是否准备加药桶',
          '上猪台是否准备',
          '加温/降温设施是否达到标准'
        ],
        rcordId: '',
        recordState: '',
        dsStatus: '',
        signerImgs: '',
        isShowSignature: true,
        billStatusIndex: '',
        canSigner: '',
        isShowBtn:false
      }
    },
    created() {
      this._getRecordDetail();
      this._getInitData();
      this.rcordId = getParam('rcordId').replace(/%3D/g, '=').trim();
    },
    computed: {
      ...mapGetters(['userInfo']),
      name() {
        return this.userInfo.name ? this.userInfo.name : ""
      }
    },
    watch: {
      recordState(newName, oldName) {
        if (this.billStatusIndex == 30 && this.canSigner == 1 && this.recordState == 1) { // 已经审核，可以签字，可以编辑
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.disabled = false;
          });
        }
        
        if(this.recordState == 2 && this.billStatusIndex == 30){ //签名完成，已完成状态，不能编辑，可以查看签名图片
          this.$nextTick(() => {
            this.isShowSignature = true;
            this.$refs.signature.fromDataURL(this.signerImgs);
            this.$refs.signature.disabled = true;
          });
        }

        if (this.recordState == 3 || this.canSigner != 1) {  // 没有审核或者已经取消
          this.isShowSignature = false;
        }
    
      }
    },
    methods: {
      _getRecordDetail() {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        // const recordId = this.$route.params.recordId;
        const recordId = getParam('rcordId').replace(/%3D/g, '=').trim();
        queryInfoById(recordId).then((res) => {
          Indicator.close();
          if (res.data) {
            let data = res.data;
            this.castData = data;
            data.state ? this.recordState = data.state : null;
            data.billStatusIndex ?  this.billStatusIndex = data.billStatusIndex : null;
            data.canSigner ? this.canSigner = data.canSigner : null;
            data.signerUrl.length ? this.signerImgs = data.signerUrl[0].img : null;
            data.actDate ? this.castData.actDate = data.actDate : null;
            data.actAvgWg ? this.castData.actAvgWg = this.castData.actAvgWg.toFixed(2) : null;
          }
        }).catch((err) => {
          Indicator.close();
        })
      },
      _getInitData() {
        this.size = JSON.parse(sessionStorage.getItem('userInfo')).size;
      },
      submitData() {
        let signerImg = this.$refs.signature.save();
        
        const data = {
          rcordId: this.rcordId,
          fileEntry: {
            imgContent: signerImg,
            imgType: 'png'
          }
        };
        
        signerTouMiao(data).then(res => {
          this.$toast({
            message: '签字确认成功！',
            duration: 1000
          });
          this.$router.push('/castSeedsRecord');
        });
      }
    }
  }
</script>
<style scoped lang="scss">
  .item-checkbox-body {
    padding: 0.04rem 0;
    min-height: 0.37rem !important;
  }
  
  .zb-checkbox {
    position: absolute;
    right: 0.35rem;
    
    span {
      position: relative;
      bottom: 0.1rem;
    }
    
    .checkboxItem {
      position: absolute;
      visibility: hidden;
      
      + label {
        position: absolute;
        width: 0.22rem;
        height: 0.22rem;
        background-color: #f89b31;
        border: 1px solid #f89b31;
        border-radius: 50%;
      }
      
      + label:after {
        content: "";
        position: absolute;
        left: 0.05rem;
        top: 0.05rem;
        width: 0.09rem;
        height: 0.06rem;
        border: 2px solid #ffffff;
        border-top-color: transparent;
        border-right-color: transparent;
        transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
        -moz-transform: rotate(-45deg);
        -webkit-transform: rotate(-45deg);
      }
    }
  }
</style>



